.Output {
  display: -ms-flexbox;
  display: flex;
  height: 112px;
  background: #fff;
}

.Output__scroll {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
}

.Output__scroll:focus {
  outline: 0;
  background-color: #e0e0e0;
}

.Output__values {
  position: absolute;
  min-width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Output__values:not(:empty) {
  cursor: pointer;
}

.Value {
  height: calc(100% - 10px);
  width: 100px;
  padding: 0 5px;
}

.Output__backspace {
  -ms-flex-item-align: center;
      align-self: center;
}

[dir='rtl'] .Output__backspace svg {
  -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
          transform: scaleX(-1);
}



