.Symbol {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  overflow: hidden;
  background: none;
}

.Symbol__container {
  position: relative;
  height: 70%;
}

.Symbol__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.Symbol__label {
  width: 100%;
  position: relative;
  height: 30%;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  display: table;
  table-layout: fixed;
}

.Symbol__label > span {
  display: table-cell;
  vertical-align: middle;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

[dir='rtl'] .Symbol__label {
  direction: rtl;
}



