.Navbar {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 48px;
  background: #222;
  color: #fff;
}

.Navbar__title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-order: 2;
      order: 2;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  pointer-events: none;
}

.Navbar__group {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
}

.Navbar__group--start {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.Navbar__group--end {
  -ms-flex-order: 3;
      order: 3;
  -ms-flex-pack: end;
      justify-content: flex-end;
}



