.EditToolbar {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 48px;
  background: #2196f3;
  color: #fff;
}

.EditToolbar__group {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
}

.EditToolbar__group--start {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.EditToolbar__group--end {
  -ms-flex-order: 3;
      order: 3;
  -ms-flex-pack: end;
      justify-content: flex-end;
}



